<template>
  <div>
    <div>
      <b-row>
        <b-col cols="8" xl="6" class="filterCard mb-2 ml-3">
          <b-col class="mb-2 mr-sm-2 mb-sm-0 alignleft title-fw">
            <label class="label mb-2 mr-sm-2 mb-sm-0">
              {{ $t("FILTER") }}
            </label>
          </b-col>
          <b-col xl="10" class="ml-5 alignright">
            <b-input-group
              :prepend="$t('ACCOUNT')"
              style="border: none; background: none"
            >
              <DropDownSearch
                :searchList="options"
                @selected_person_data="getSelectedAccount"
                class="searchSelect1"
              >
              </DropDownSearch>
              <b-checkbox
                v-model="useState"
                @change="displayUse"
                class="checkBox"
              >
                {{ $t("USE") }}
              </b-checkbox>
              <b-checkbox
                v-model="unUsedState"
                @change="displayUnUse"
                class="checkBox"
                >{{ $t("UNUSED") }}
              </b-checkbox>
            </b-input-group>
          </b-col>
        </b-col>
        <b-col cols="8" xl="4" class="mb-0 add-button">
          <b-button
            @click="addExpenseItem"
            variant="primary"
            class="text-md mb-2"
            >{{ $t("ADD.EXPENSE") }}</b-button
          >
        </b-col>
      </b-row>
      <b-row class="table-height">
        <b-col xl="12">
          <b-table
            ref="selectableTable"
            :select-mode="selectModeTable"
            :fields="fieldss"
            :items="tableData"
            :per-page="limit"
            :total="total"
            :startPage="start"
            @change-page="pageChange"
            :busy="isBusy"
            :striped="false"
            :borderless="false"
            :outlined="false"
            head-variant="light"
            small="small"
            fixed
            sticky-header
            show-empty
            responsive="sm"
          >
            <template #table-colgroup="scope">
              <col
                v-for="field in scope.fields"
                :key="field.key"
                :style="{
                  width: field.key === 'checkState' ? '30px' : '110px',
                }"
              />
            </template>
            <template v-slot:cell(checkState)="item">
              <b-form-radio
                :id="'radio-' + item.index"
                v-model="item.item.checkState"
                :name="'table-radio'"
                @change="toggleSelectAll(item)"
              ></b-form-radio>
            </template>
            <template v-slot:cell(AccountCode)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(AccountName)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(ExpenseItemCode)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(ExpenseItemName)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(UseFlag)="item">{{
              $t(item.value.toString().toUpperCase())
            }}</template>
            <template v-slot:cell(CreatedDate)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(UpdateDate)="item">{{ item.value }}</template>
            <template v-slot:cell(Icon)="item">
              <b-button
                @click="editExpenseItem(item)"
                variant="primary"
                class="Detailsbtn"
                >{{ $t("EDIT") }}</b-button
              >
            </template>
            <template v-slot:empty>
              <div class="text-center errorColor">
                {{ $t("GENRALDATAERRORMESSAGE") }}
              </div>
            </template>
            <template v-slot:table-busy>
              <div class="text-center text-danger my-2">
                <b-spinner class="align-middle"></b-spinner>
              </div>
            </template>
          </b-table>
        </b-col>
      </b-row>
    </div>
    <b-row>
      <b-col cols="12" xl="4">
        <b-button
          @click="deleteSelectedAccount"
          :disabled="!(checkedItems.length === 1) || loading"
          variant="primary"
        >
          <span>{{ $t("DELETEADDEDEXPENSEITEM") }}</span>
          <i v-if="loading" class="fa fa-spinner fa-spin" />
        </b-button>
      </b-col>
      <b-col cols="12" xl="5" class="mt-1">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          class="paginateProcess"
          align="center"
          size="sm"
        >
          <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
          <template v-slot:next-text>{{ $t("NEXT") }}</template>
          <template v-slot:page="{ page, active }">
            <strong v-if="active">{{ page }}</strong>
            <i v-else>{{ page }}</i>
          </template>
        </b-pagination>
      </b-col>
      <b-col cols="12" xl="3" class="text-center">
        <div class="countList">
          <label>{{ $t("MEANS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-link :key="index" v-if="limit == page" href="javascript:;">
              {{ page }}
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
            <b-link
              :key="index"
              @click="perPageChange(page)"
              v-else
              href="javascript:;"
            >
              <u>{{ page }}</u>
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
          </template>
        </div>
      </b-col> </b-row
    ><br />
  </div>
</template>

<script>
import DropDownSearch from "@/components/DropDownSearch.vue";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
import _ from "lodash";
import i18n from "@/plugins/i18n";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "MasterMaintenanceList",
  components: {
    DropDownSearch,
  },
  data() {
    return {
      selectModeTable: "single",
      options: [],
      limit: 50,
      total: 0,
      start: 0,
      currentPage: 1,
      pageOptions: [50, 100, 200],
      isBusy: false,
      tableData: [],
      accountCode: [],
      firstCall: true,
      useState: false,
      unUsedState: false,
      selectedItems: [],
      checkedItems: [],
      loading: false,
    };
  },
  computed: {
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    fieldss() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-center",
        },
        {
          key: "AccountCode",
          label: this.$t("MASTERMAINTENANCE.ACCOUNTCODE"),
          class: "text-center",
        },
        {
          key: "AccountName",
          label: this.$t("MASTERMAINTENANCE.ACCOUNTNAME"),
          class: "text-left title-normal-width",
        },
        {
          key: "ExpenseItemCode",
          label: this.$t("MASTERMAINTENANCE.EXPENSEITEMCODE"),
          class: "text-center",
        },
        {
          key: "ExpenseItemName",
          label: this.$t("MASTERMAINTENANCE.EXPENSEITEMNAME"),
          class: "text-left title-normal-width",
        },
        {
          key: "UseFlag",
          label: this.$t("MASTERMAINTENANCE.USEFLAG"),
          class: "text-center",
        },
        {
          key: "CreatedDate",
          label: this.$t("MASTERMAINTENANCE.CREATEDDATE"),
          class: "text-center",
        },
        {
          key: "UpdateDate",
          label: this.$t("MASTERMAINTENANCE.UPDATEDATE"),
          class: "text-center",
        },
        {
          key: "Icon",
          label: "",
          class: "text-center",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.firstCall = true;
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  mounted() {
    this.getTaskListData();
  },
  methods: {
    /**
     * @description function to get the rows count
     * @name getRowCount
     * @param none
     * @returns integer
     */
    getRowCount: function () {
      return this.items.length;
    },
    /**
     * @description function to get the items from table data
     * @name items
     * @param none
     * @returns nothing
     */
    items() {
      return this.tableData;
    },
    /**
     * @description function to get rows count
     * @name rows
     * @param none
     * @returns nothing
     */
    rows() {
      return this.tableData.length;
    },
    /**
     * @description function to get the data when page change event rised
     * @name perPageChange
     * @param page
     * @returns nothing
     */
    perPageChange(page) {
      this.limit = page;
      this.getTaskListData();
    },
    /**
     * @description function to get the list as per the limit of the table
     * @name lists
     * @param none
     * @returns items 's per the page limit'
     */
    lists() {
      const items = this.tableData;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
    /**
     * @description function to update the store and navigate to expense item add/edit page
     * @name editExpenseItem
     * @param item
     * @returns nothing
     */
    editExpenseItem(item) {
      this.$store.dispatch("updateMasterData", {
        expenseMasterData: item,
      });
      this.$router.push({ name: "expense_item_add_edit" });
    },
    /**
     * @description function to update the store and navigate to expense item add/edit page
     * @name addExpenseItem
     * @param none
     * @returns nothing
     */
    addExpenseItem() {
      this.$store.dispatch("updateMasterData", {
        expenseMasterData: null,
      });
      this.$router.push({ name: "expense_item_add_edit" });
    },
    /**
     * @description function to toggle the checkboxes in the table
     * @name toggleSelectAll
     * @param item
     * @returns nothing
     */
    toggleSelectAll(item) {
      this.checkedItems = [];
      let selectedRow = item.item;
      this.checkedItems.push(selectedRow);
    },
    /**
     * @description function to get the selected account
     * @name getSelectedAccount
     * @param item
     * @returns nothing
     */

    getSelectedAccount(item) {
      if (item.acc_code) {
        this.accountCode = [];
        let temp = _.find(this.options, function (o) {
          return o.acc_code === item.acc_code;
        });
        this.accountCode.push(temp.acc_code);
        this.getTaskListData();
      } else {
        this.accountCode = [];
        this.unUsedState = false;
        this.useState = false;
        this.getTaskListData();
      }
    },
    /**
     * @description function to get the task list based on the use state
     * @name displayUse
     * @param data
     * @returns nothing
     */

    displayUse(data) {
      this.useState = data;
      this.getTaskListData();
    },
    /**
     * @description function to get the task list based on the unUse state
     * @name displayUnUse
     * @param data
     * @returns nothing
     */

    displayUnUse(data) {
      this.unUsedState = data;
      this.getTaskListData();
    },
    /**
     * @description function to get the task list data
     * @name getTaskListData
     * @param none
     * @returns nothing
     */

    async getTaskListData() {
      this.checkedItems = [];
      // let personID = "";
      this.isBusy = true;
      if (this.displayCheck) {
        //personID = 3;
        // personID = localStorage.getItem("personID");
      }
      let tempArr = [];
      if (!(this.useState && this.unUsedState)) {
        if (this.useState) {
          tempArr.push(true);
        } else if (this.unUsedState) {
          tempArr.push(false);
        } else {
          tempArr.push(true, false);
        }
      } else {
        tempArr.push(true, false);
      }
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READWITHJOINMEXPENSEITEM,
        {
          limit: this.limit,
          offset: this.start * this.limit,
          paginate: true,
          fieldlist: [
            "m_expense_item.accounts_name_cd",
            "m_accounts_name.accounts_name_nm",
            "m_expense_item.expense_item_cd",
            "m_expense_item.expense_item_nm",
            "m_expense_item.use_flg",
            "m_expense_item.create_dt",
            "m_expense_item.update_dt",
          ],
          conditions:
            this.accountCode.length === 0
              ? tempArr.length === 1
                ? [
                    {
                      key: "m_expense_item.use_flg",
                      value: tempArr,
                      operator: "=",
                    },
                  ]
                : []
              : tempArr.length === 1
              ? [
                  {
                    key: "m_expense_item.accounts_name_cd",
                    value: this.accountCode,
                    operator: "=",
                  },
                  {
                    key: "m_expense_item.use_flg",
                    value: tempArr,
                    operator: "=",
                  },
                ]
              : [
                  {
                    key: "m_expense_item.accounts_name_cd",
                    value: this.accountCode,
                    operator: "=",
                  },
                ],
          joins: [
            {
              table: "m_accounts_name",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_accounts_name.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.accounts_name_cd",
                },
              ],
            },
          ],
        }
      );
      if (data.success) {
        this.total = 0;
        this.tableData = [];
        let responseData = data.data;
        let tempData = [];
        if (responseData.data.length > 0) {
          this.total = data.data.total;
          for (let i = 0; i < responseData.data.length; i++) {
            if (
              responseData.data[i].accounts_name_cd &&
              responseData.data[i].expense_item_cd
            ) {
              tempData.push({
                AccountCode: responseData.data[i].accounts_name_cd,
                AccountName: responseData.data[i].accounts_name_nm,
                ExpenseItemCode: responseData.data[i].expense_item_cd,
                ExpenseItemName: responseData.data[i].expense_item_nm,
                UseFlag: responseData.data[i].use_flg,
                CreatedDate: responseData.data[i].create_dt
                  ? this.getDate(responseData.data[i].create_dt)
                  : "",
                UpdateDate: responseData.data[i].update_dt
                  ? this.getDate(responseData.data[i].update_dt)
                  : "",
              });
            }
          }
          this.tableData = _.orderBy(tempData, "ExpenseItemCode", "asc");
          if (this.firstCall) {
            this.options = [];
            let tempOption = [];
            for (let i = 0; i < responseData.data.length; i++) {
              tempOption.push({
                value: i,
                text: responseData.data[i].accounts_name_nm,
                acc_code: responseData.data[i].accounts_name_cd,
                exp_code: responseData.data[i].expense_item_cd,
              });
            }
            this.firstCall = false;
            tempOption = _.uniqBy(tempOption, "acc_code");
            this.options = _.orderBy(tempOption, "exp_code", "asc");
          }
          this.isBusy = false;
        } else {
          this.isBusy = false;
          this.tableData = [];
        }
      } else {
        this.isBusy = false;
        this.tableData = [];
      }
    },
    /**
     * @description function to get formatted date
     * @name getDate
     * @param date
     * @returns string 'formatted date'
     */
    getDate(date) {
      if (!date) {
        return "";
      }
      return (
        date.slice(0, 4) + "/" + date.slice(5, 7) + "/" + date.slice(8, 10)
      );
    },
    /**
     * @description function which trriger when the pagination button clicks
     * @name pageChange
     * @param page
     * @returns nothing
     */
    pageChange(page) {
      this.start = page - 1;
      this.getTaskListData();
    },
    /**
     * @description function to delete the selected account
     * @name deleteSelectedAccount
     * @param none
     * @returns nothing
     */
    async deleteSelectedAccount() {
      let self = this;
      this.$swal({
        text: i18n.t("DELETEEXPENSEITEMMESSAGE"),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: i18n.t("YESDELETE"),
        cancelButtonText: i18n.t("NODELETE"),
      }).then(async function (isconfirm) {
        if (isconfirm.value == true) {
          let itemList = [];
          for (let i = 0; i < self.checkedItems.length; i++) {
            itemList.push(self.checkedItems[i].ExpenseItemCode);
          }
          self.loading = true;
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERSOFTDELETEMEXPENSEITEM,
            {
              conditions: [
                {
                  key: "expense_item_cd",
                  value: itemList,
                },
              ],
              update_id: localStorage.getItem("personID"),
            }
          );
          self.loading = false;
          if (data.success) {
            self
              .$swal({ type: "success", text: i18n.t("DELETED") })
              .then(async function () {
                self.getTaskListData();
              });
          } else {
            self.$swal({ type: "error", text: i18n.t("NOTDELETED") });
          }
        }
      });
    },
  },
};
</script>

<style scoped lang="scss">
