  width: 80px !important;
  padding-top: 15px !important;
}
.custom-select {
  height: 30px;
  border-color: rgb(163, 163, 163);
}

.text-sm {
  font-size: 10px;
}
.filterCard {
  padding-top: 2px;
  padding-bottom: 4px;
  border: 1px dashed;
}
.filter-style {
  font-weight: bold;
}
.card-body {
  padding-right: 0;
}
.unused {
  padding-right: 0;
}
.text-style {
  white-space: nowrap;
}
.button-style {
  display: flex;
  align-items: center;
}
.countList {
  float: right;
  padding-top: 10px;
}
